import React, { useEffect, useState } from "react";
import './style.css'
import $ from "jquery";
import { useNuiEvent } from "../../../../hooks/useNuiEvent";
import { useLocation } from "react-router-dom";
import DataTable, { createTheme } from "react-data-table-component";

const customStyles = {
    rows: {
      style: {
        minHeight: "2vw", // override the row height
        minWidth: "45vw"
      },
    },
    table: {
      style: {
        maxHeight: "26vw",
        maxWidth: "47vw"
      },
    },
  };
  
createTheme("solarized", {
    text: {
      primary: "#131313",
      secondary: "#2aa198",
    },
    overflowY: true,
    background: {
      default: "#f1f1f1f1",
    },
    context: {
      background: "#131313",
      text: "#FFFFFF",
    },
  
    action: {
      button: "rgba(0,0,0,.54)",
      hover: "rgba(0,0,0,.08)",
      disabled: "rgba(0,0,0,.12)",
    },
});

let prisoes: any
const Procurar = () => {
    const [count, setCount] = useState(0);
    const [nome,setNome] = useState("");
    const [rg,setRG] = useState("");
    const [foragido,setForagido] = useState(1);
    const [licensa,setLicensa] = useState(1);
    const [id,setId] = useState("");
    const [phone,setPhone] = useState("");
    const [idade,setIdade] = useState("");
    const [multas,setMultas] = useState("");

    $.post("http://eg_policia/getProcurado",JSON.stringify({
          id: $("#idProcurado").val(),
    }))

    let location = useLocation();
    useNuiEvent('setProcurado',(data2) => {
        setNome(data2[0].name + " " + data2[0].firstname)
        setRG(data2[0].registration)
        setForagido(data2[0].foragido)
        setId(data2[0].user_id)
        setIdade(data2[0].age)
        setLicensa(data2[0].licensa)
        setPhone(data2[0].phone)
        setMultas(data2[1])
        prisoes = data2[2]
        setCount(1)
    })
    if (location.pathname == "/procurar") {
        
        if (count == 1) {
            const columns = [
                {
                  name: "Policial:",
                  selector: (data: any) => data.police_id,
                  sortable: true,
                  maxWidth: "1vw",
                },
                {
                  name: "Descricao:",
                  selector: (data: any) => data.text,
                  sortable: true,
                },
                {
                  name: "Multa Recebida:",
                  selector: (data: any) => new Intl.NumberFormat('pt-BR', { style: 'currency', currency: 'BRL' }).format(data.valor),
                  sortable: true,
                },
                {
                  name: "Data:",
                  selector: (data: any) => new Intl.DateTimeFormat('en-US', {year: 'numeric', month: '2-digit',day: '2-digit', hour: '2-digit', minute: '2-digit'}).format(data.horario),
                  sortable: true,
                  minHeight: "20vw",
                },
            ];

            return (
                <div className="content-procurar">
                    <div className="content">
                        <header>
                            <h1>Procurar ID</h1>
                        </header>
                        <div className="player-informations">
                            <div className="left">
                                <div className="item">
                                    <label>NOME:</label>
                                    <h1>{nome}</h1>
                                </div>
                                <div className="item">
                                    <label>RG:</label>
                                    <h1>{rg}</h1>
                                </div>
                                <div className="item">
                                    <label>MULTAS:</label>
                                    <h1>{multas}</h1>
                                </div>
                            </div>
                            <div className="center">
                                <div className="item">
                                    <label>PASSAPORTE:</label>
                                    <h1>{id}</h1>
                                </div>
                                <div className="item">
                                    <label>IDADE:</label>
                                    <h1>{idade}</h1>
                                </div>
                                <div className="item">
                                    <label>TEL:</label>
                                    <h1>{phone}</h1>
                                </div>
                            </div>
                            <div className="right">
                                <div className="item foragido">
                                    <label>FORAGIDO:</label>
                                    <h1>{foragido == 1 ? "SIM" : "NÃO"}</h1>
                                </div>
                                <div className="item">
                                    <label>PORTE:</label>
                                    <h1>{licensa == 1 ? "SIM" : "NÃO"}</h1>
                                </div>
                            </div>
                        </div>
                        <div className="data-table-acoes">
                        <DataTable
                            columns={columns}
                            data={prisoes}
                            data-tag="allowRowEvents"
                            customStyles={customStyles}
                            theme="solarized"
                            fixedHeader
                        />
                        </div>
                    </div>
                </div>
            );
        }else{
            return(<div></div>)
        }
    }
};

export default Procurar