import React, { useState } from "react";
import Modal from "react-modal";
import FadeIn from "react-fade-in";
import "./style.css";
import { useLocation } from "react-router-dom";
import DataTable, { createTheme } from "react-data-table-component";
import Select from 'react-select';
import makeAnimated from 'react-select/animated';
import $ from 'jquery';
import { useNuiEvent } from "../../../../hooks/useNuiEvent";

const animatedComponents = makeAnimated();

const valPercent = [
  { value: 1.0, label: '100% do valor'},
  { value: 0.9, label: '90% do valor' },
  { value: 0.8, label: '80% do valor' },
  { value: 0.7, label: '70% do valor' },
  { value: 0.6, label: '60% do valor' },
  { value: 0.5, label: '50% do valor' },
  { value: 0.4, label: '40% do valor' },
  { value: 0.3, label: '30% do valor' },
  { value: 0.2, label: '20% do valor' },
  { value: 0.1, label: '10% do valor' },
  { value: 0.0, label:  '0% do valor' },
]

const customStyles = {
  rows: {
    style: {
      minHeight: "2vw", // override the row height
      minWidth: "45vw"
    },
  },
  table: {
    style: {
      maxHeight: "29vw",
      maxWidth: "47vw"
    },
  },
};

createTheme("solarized", {
  text: {
    primary: "#131313",
    secondary: "#2aa198",
  },

  overflowY: false,
  background: {
    default: "#f1f1f1f1",
  },
  context: {
    background: "#131313",
    text: "#FFFFFF",
  },
  innerHeight: "10vw",

  action: {
    button: "rgba(0,0,0,.54)",
    hover: "rgba(0,0,0,.08)",
    disabled: "rgba(0,0,0,.12)",
  },
});

const columns = [
  {
    name: "Art:",
    selector: (data: any) => data.art,
    sortable: true,
    maxWidth: "1vw",
    minWidth: "5vw",
  },
  {
    name: "Crimes:",
    selector: (data: any) => data.crime,
    sortable: true,
    maxWidth: "25vw",
    minWidth: "25vw",
  },
  {
    name: "Serviços:",
    selector: (data: any) => data.servicos + " Meses",
    sortable: true,
    maxWidth: "3vw",
  },
  {
    name: "Multa:",
    selector: (data: any) => new Intl.NumberFormat('pt-BR', { style: 'currency', currency: 'BRL' }).format(data.multa),
    sortable: true,
    minWidth: "8vw",
  },
];

let totalTempoPreso:any = 0
let totalMulta:any = 0
const CodigoPenal = () => {
  useNuiEvent('updatePreso',() => {
    totalTempoPreso = 0
    totalMulta = 0
    pena = []
    selectParticipantesValue = ""
    selectPercentValue = ""
    selectPercentValue2 = ""
  })

  const [modalIsOpen, setModalIsOpen] = useState(false);
  let pena:any = []

  const handleChange = (state: { selectedRows: any; }) => {
    pena = state.selectedRows
  };
  
  let selectParticipantesValue = ""
  function selectParticipantes(inputValue:any, actionMeta:any) {
    selectParticipantesValue = inputValue
  }

  let selectPercentValue = ""
  function selectPercent(inputValue:any, actionMeta:any) {
    selectPercentValue = inputValue
  }

  let selectPercentValue2 = ""
  function selectPercent2(inputValue:any, actionMeta:any) {
    selectPercentValue2 = inputValue
  }

  function setPreso() {
    $.post("http://eg_policia/setPreso",JSON.stringify({
      id: $("#idPreso").val(),
      desc: $("#descPreso").val(),
      totalMulta: totalMulta,
      totalTempoPreso: totalTempoPreso,
      participantes: selectParticipantesValue,
      multa: selectPercentValue,
      preso: selectPercentValue2,
    }));
    closeModal();
  }

  function openModal() {
    for (let i = 0; i < pena.length; i++) {
      const atual = pena[i];
      totalMulta = totalMulta + atual.multa
      totalTempoPreso = totalTempoPreso + atual.servicos
    }
    setModalIsOpen(true);
  }

  function closeModal() {
    setModalIsOpen(false);
    totalTempoPreso = 0
    totalMulta = 0
    pena = []
    selectParticipantesValue = ""
    selectPercentValue = ""
    selectPercentValue2 = ""
  }

  let location = useLocation();
  if (location.pathname == "/codigo-penal") {
    let data:any = localStorage.getItem('codigoPenal')
    data = JSON.parse(data)

    let funcionarios:any = localStorage.getItem('funcionarios')
    funcionarios = JSON.parse(funcionarios)

    return (
      <div className="content-penal">
        <div className="content">
          <FadeIn>
            <header>
              <h1>Sistema de prisão</h1>
              <button onClick={openModal} >Prender</button>
            </header>
            <div className="content-tables">
              <div className="data-table-codigo-penal">
                <DataTable
                  columns={columns}
                  data={data}
                  selectableRows
                  data-tag="allowRowEvents"
                  customStyles={customStyles}
                  theme="solarized"
                  fixedHeader
                  onSelectedRowsChange={handleChange}
                />
              </div>
            </div>
            <Modal
              isOpen={modalIsOpen}
              onRequestClose={closeModal}
              className="prender-modal"
            >
              <div className="modal-prender">
                <div className="modal">
                  <div className="content">
                    <h1>Prender cidadão</h1>
                    <p>Aqui você pode prender um cidadão</p>
                    <div className="inputs">
                      <label htmlFor="">ID do preso:</label>
                      <input id="idPreso" type="number" placeholder="1" required/>
                      <label htmlFor="">Policiais participantes:</label>
                      <Select
                        className="selectParticipantes"
                        closeMenuOnSelect={false}
                        components={animatedComponents}
                        isMulti
                        options={funcionarios}
                        onChange={selectParticipantes}
                      />
                      <label htmlFor="">% Multa Aplicada: {totalMulta}</label>
                      <Select
                        className="selectParticipantes"
                        closeMenuOnSelect={false}
                        components={animatedComponents}
                        options={valPercent}
                        onChange={selectPercent}
                      />
                      <label htmlFor="">% Prisão Aplicada: {totalTempoPreso}</label>
                      <Select
                        className="selectParticipantes"
                        closeMenuOnSelect={false}
                        components={animatedComponents}
                        options={valPercent}
                        onChange={selectPercent2}
                      />
                      <label htmlFor="">Descrição da prisão:</label>
                      <input id="descPreso" type="text" placeholder="Descrição..."/>
                      <br></br>
                    </div>
                    <button className="prender-btn" onClick={setPreso} >PRENDER</button>
                  </div>
                </div>
              </div>
            </Modal>
          </FadeIn>
        </div>
      </div>
    );
  } else {
    <div></div>;
  }
};

export default CodigoPenal;
