import React from 'react';
import Button from './components/Button';
import TopSidebar from './components/Top';
import { AiFillDashboard,AiFillHome,AiOutlineUsergroupAdd,AiOutlineFileSearch,AiOutlineBorderlessTable,AiOutlineFileAdd } from 'react-icons/ai'
import { GiPistolGun,GiDistraction } from "react-icons/gi";
import './style.css'
import FadeIn from "react-fade-in";
import { Link } from 'react-router-dom';

interface SidebarProps {
    image: string;
    name: string;
}
const Sidebar = ({ image, name }: SidebarProps) => {
    return (
        <div className="sidebar">
            <div className="content">
                <FadeIn>
                    <div className="top-sidebar">
                        <img src={image} alt="" />
                        <h1>{name}</h1>
                    </div>
                    <div className="sidebar-btns">
                        <div className="sidebar-btn">
                            <Link to='/'><span><AiFillHome /> Home</span></Link>
                            <Link to='/acoes'><span><GiDistraction /> Ações</span></Link>
                            <Link to='/arsenal'><span><GiPistolGun /> Arsenal</span></Link>
                            <Link to='/funcionarios'><span><AiOutlineUsergroupAdd /> Funcionários</span></Link>
                            <Link to='/informacoes'><span><AiOutlineUsergroupAdd /> Informações</span></Link>
                            <Link to='/codigo-penal'><span><AiOutlineBorderlessTable /> Sistema Penal</span></Link>
                            <Link to='/boletim'><span><AiOutlineFileAdd /> Multar</span></Link>
                        </div>
                    </div>
                </FadeIn>
            </div>
        </div >
    )
}
export default Sidebar;