import React, { useState } from "react";
import ReactApexChart from 'react-apexcharts'
import { ApexOptions } from 'apexcharts';
import './style.css'
import FadeIn from "react-fade-in";
import { useNuiEvent } from "../../../../hooks/useNuiEvent";
import $ from 'jquery';

let renderizado = 0
const Dashboard = (props: any) => {
    // const [ponto, setPonto] = useState("ABRIR-PONTO")

    useNuiEvent('setData',(data) => {
        renderizado = 1
        localStorage.clear()
        localStorage.setItem('nomePolicia',data[0])
        localStorage.setItem('imgPolicia',data[1])
        localStorage.setItem('nomePolicial',data[2])
        localStorage.setItem('identidadePolicial',JSON.stringify(data[3]))
        localStorage.setItem('usuario',JSON.stringify(data[4]))
        localStorage.setItem('funcionarios',JSON.stringify(data[5]))
        localStorage.setItem('acoes',JSON.stringify(data[6]))
        localStorage.setItem('bateponto',JSON.stringify(data[7]))
        localStorage.setItem('codigoPenal',JSON.stringify(data[8]))
        localStorage.setItem('arsenal',JSON.stringify(data[9]))
        localStorage.setItem('patentes',JSON.stringify(data[10]))
        localStorage.setItem('unidades',JSON.stringify(data[11]))
        localStorage.setItem('isComando',JSON.stringify(data[12]))
        localStorage.setItem('pontuacaoTotal',data[13])
        localStorage.setItem('pontosGraph',JSON.stringify(data[14]))
        localStorage.setItem('prisoes',JSON.stringify(data[15]))
        localStorage.setItem('prisoesHoje',JSON.stringify(data[16]))
        localStorage.setItem('fardamentos',JSON.stringify(data[17]))
        localStorage.setItem('viaturas',JSON.stringify(data[18]))
    })

    if (renderizado == 1) {
        let usuario:any = localStorage.getItem('usuario')
        usuario = JSON.parse(usuario)

        let name = localStorage.getItem('nomePolicial')
        let pontuacaoTotal = localStorage.getItem('pontuacaoTotal')
        let prisoes = localStorage.getItem('prisoes')
        let prisoesHoje = localStorage.getItem('prisoesHoje')

        const dataGraph = []
        let data = new Date()

        data.setMonth(data.getMonth() + 1)
        dataGraph.push(data.getFullYear()+"-"+data.getMonth()+"-"+data.getDate()+"T00:00:00.000Z")
        data.setDate(data.getDate() - 1);
        dataGraph.push(data.getFullYear()+"-"+data.getMonth()+"-"+data.getDate()+"T00:00:00.000Z")
        data.setDate(data.getDate() - 1);
        dataGraph.push(data.getFullYear()+"-"+data.getMonth()+"-"+data.getDate()+"T00:00:00.000Z")
        data.setDate(data.getDate() - 1);
        dataGraph.push(data.getFullYear()+"-"+data.getMonth()+"-"+data.getDate()+"T00:00:00.000Z")
        data.setDate(data.getDate() - 1);
        dataGraph.push(data.getFullYear()+"-"+data.getMonth()+"-"+data.getDate()+"T00:00:00.000Z")
        data.setDate(data.getDate() - 1);
        dataGraph.push(data.getFullYear()+"-"+data.getMonth()+"-"+data.getDate()+"T00:00:00.000Z")
        data.setDate(data.getDate() - 1);
        dataGraph.push(data.getFullYear()+"-"+data.getMonth()+"-"+data.getDate()+"T00:00:00.000Z")

        const options: ApexOptions = {
            chart: {
                toolbar: {
                    show: false,
                },
                zoom: {
                    enabled: false,
                },
                foreColor: "black",
            },
            colors: ['#d00d0d', '#d80000', '#d00d0d'],
            xaxis: {
                type: 'datetime',
                categories: dataGraph,
            },
        };

        let pontosGraph:any = localStorage.getItem('pontosGraph')
        pontosGraph = JSON.parse(pontosGraph)
        
        const series = [{
            name: 'Pontos',
            data: pontosGraph
        }]
        
        let ponto = "ABRIR-PONTO"
        let pontoAberto = localStorage.getItem('bateponto')
        const handlePonto = () => {
            if (pontoAberto == "0") {
                localStorage.setItem('bateponto',"1")
                $.post("http://eg_policia/baterPonto",JSON.stringify({
                    data: true
                }))
                ponto = "FECHAR-PONTO"
                let buttonPonto = document.getElementById("header-button");
                buttonPonto?.classList.add("vermelho");
            } else {
                localStorage.setItem('bateponto',"0")
                $.post("http://eg_policia/baterPonto",JSON.stringify({
                    data: false
                }))
                ponto = "ABRIR-PONTO"
                let buttonPonto = document.getElementById("header-button");
                buttonPonto?.classList.remove("vermelho");
            }
        }

        const submitSearch = (e: any,) => {
            e.preventDefault();
            props.history.push('/procurar')
        }

        return (
            <div className="content-dashboard">
                <div className="content">
                    <FadeIn>
                        <header className="header-dashboard">
                            <h1>Olá, <span>{name}</span></h1>
                            {/* <button onClick={handlePonto} id="header-button">{ponto}</button> */}
                            <form onSubmit={submitSearch}>
                                <input id="idProcurado" type="number" placeholder="Procurar cidadão" />
                            </form>
                        </header>
                        <div className="summary-content">
                            <div className="summary">
                                <span>N° Registro:</span>
                                <h1>#{usuario.id}</h1>
                            </div>
                            <div className="summary">
                                <span>Pontuação Total:</span>
                                <h1>{pontuacaoTotal}</h1>
                            </div>
                            <div className="summary">
                                <span>Prisões Totais:</span>
                                <h1>{prisoes}</h1>
                            </div>
                            <div className="summary">
                                <span>Prisões hoje:</span>
                                <h1>{prisoesHoje}</h1>
                            </div>
                        </div>
                        <ReactApexChart className="graph" options={options} series={series} height={340} type="line"/>
                    </FadeIn>
                </div>
            </div>
        )
    } else{
        return(<div></div>);
    }
}

export default Dashboard;