import React, { useState } from "react";
import FadeIn from "react-fade-in";
import Modal from "react-modal";
import "./style.css";
import DataTable, { createTheme } from "react-data-table-component";
import Select from 'react-select';
import makeAnimated from 'react-select/animated';
import { useLocation } from "react-router-dom";
import { useNuiEvent } from "../../../../hooks/useNuiEvent";
import $ from 'jquery';

const animatedComponents = makeAnimated();
const result = [
    { value: 'Vitória', label: 'Vitória' },
    { value: 'Derrota', label: 'Derrota' },
    { value: 'Empate', label: 'Empate' }
]

const customStyles = {
  rows: {
    style: {
      minHeight: "2vw", // override the row height
      minWidth: "45vw"
    },
  },
  table: {
    style: {
      maxHeight: "31vw",
      maxWidth: "47vw"
    },
  },
};

createTheme("solarized", {
  text: {
    primary: "#131313",
    secondary: "#2aa198",
  },
  overflowY: true,
  background: {
    default: "#f1f1f1f1",
  },
  context: {
    background: "#131313",
    text: "#FFFFFF",
  },

  action: {
    button: "rgba(0,0,0,.54)",
    hover: "rgba(0,0,0,.08)",
    disabled: "rgba(0,0,0,.12)",
  },
});

const Acoes = () => {

  useNuiEvent('updateAcoes',(data) => {
    localStorage.setItem('acoes',JSON.stringify(data))
  })

  const columns = [
    {
      name: "Local da Ação:",
      selector: (data: any) => data.text,
      sortable: true,
    },
    {
      name: "Dinheiro Furtado:",
      selector: (data: any) => new Intl.NumberFormat('pt-BR', { style: 'currency', currency: 'BRL' }).format(data.valor),
      sortable: true,
    },
    {
      name: "Data:",
      selector: (data: any) => new Intl.DateTimeFormat('en-US', {year: 'numeric', month: '2-digit',day: '2-digit', hour: '2-digit', minute: '2-digit'}).format(data.horario),
      sortable: true,
      minHeight: "20vw",
    },
    {
      name: "Resultado:",
      selector: (data: any) => <button className="finalizar" onClick={()=>openModal(data)}>FINALIZAR</button>,
      sortable: true,
    },
  ];

  const [modalIsOpen, setModalIsOpen] = useState(false);
  const [data,setData] = useState([]);

  function openModal(data: any) {
    setData(data)
    setModalIsOpen(true);
  }

  function closeModal() {
    setModalIsOpen(false);
  }

  let selectParticipantesValue = ""
  function selectParticipantes(newValue:any, actionMeta:any) {
    selectParticipantesValue = newValue
  }  
  
  let selectResultadoValue = ""
  function selectResultado(newValue:any, actionMeta:any) {
    selectResultadoValue = newValue
  }  

  const finalizarAcao = () => {
    $.post("http://eg_policia/finalizarAcao",JSON.stringify({
      data: data,
      participantes: selectParticipantesValue,
      result: selectResultadoValue,
      desc: $("#relatorio").val(),
    }));
    closeModal();
  };

  let location = useLocation();
  if (location.pathname == "/acoes") {
    let acoes: any = localStorage.getItem("acoes");
    acoes = JSON.parse(acoes);

    let funcionarios: any = localStorage.getItem("funcionarios");
    funcionarios = JSON.parse(funcionarios);

    return (
      <div className="content-acoes">
        <div className="content">
          <FadeIn>
            <h1>Lista de ações</h1>
            <div className="data-table-acoes">
              <DataTable
                columns={columns}
                data={acoes}
                data-tag="allowRowEvents"
                customStyles={customStyles}
                theme="solarized"
                fixedHeader
              />
            </div>
            <Modal
              isOpen={modalIsOpen}
              onRequestClose={closeModal}
              className="prender-modal"
            >
              <div className="modal-prender">
                <div className="modal">
                  <div className="content">
                    <h1>Finalizar ação</h1>
                    <p>Aqui você pode finalizar uma ação</p>
                    <div className="inputs">
                      <label htmlFor="">Resultado da ação</label>
                      <Select
                        className="select-modal"
                        closeMenuOnSelect={false}
                        components={animatedComponents}
                        options={result}
                        onChange={selectResultado}
                      />
                      <label htmlFor="">Participantes da ação</label>
                      <Select
                        className="select-modal"
                        closeMenuOnSelect={false}
                        components={animatedComponents}
                        isMulti
                        options={funcionarios}
                        onChange={selectParticipantes}
                      />
                      <label htmlFor="">Relatório da ação:</label>
                      <input id="relatorio" type="text" placeholder="Apreendemos x armas..."/>
                    </div>
                    <button className="enviar-btn" onClick={finalizarAcao}>ENVIAR RELATÓRIO</button>
                  </div>
                </div>
              </div>
            </Modal>
          </FadeIn>
        </div>
      </div>
    );
  }
};

export default Acoes;