import React, { useState } from "react";
import "./style.css";
import Modal from "react-modal";
import { useLocation } from "react-router-dom";
import DataTable, { createTheme } from "react-data-table-component";
import makeAnimated from 'react-select/animated';
import $ from 'jquery';

const animatedComponents = makeAnimated();

const customStyles = {
  rows: {
    style: {
      minHeight: "2vw", // override the row height
      minWidth: "45vw"
    },
  },
  table: {
    style: {
      maxHeight: "29vw",
      maxWidth: "47vw"
    },
  },
};

createTheme("solarized", {
  text: {
    primary: "#131313",
    secondary: "#2aa198",
  },

  overflowY: false,
  background: {
    default: "#f1f1f1f1",
  },
  context: {
    background: "#131313",
    text: "#FFFFFF",
  },
  innerHeight: "10vw",

  action: {
    button: "rgba(0,0,0,.54)",
    hover: "rgba(0,0,0,.08)",
    disabled: "rgba(0,0,0,.12)",
  },
});

const columns = [
  {
    name: "Patente:",
    selector: (data: any) => data.patente,
    maxWidth: "5vw",
    minWidth: "8vw",
  },
  {
    name: "Preset:",
    selector: (data: any) => data.roupa,
  },
];

const columnsViaturas = [
  {
    name: "Patente:",
    selector: (vtr: any) => vtr.patente,
    maxWidth: "5vw",
    minWidth: "8vw",
  },
  {
    name: "Viatura:",
    selector: (vtr: any) => vtr.vtr,
  },
];

const Informacoes = () => {
  const [modalIsOpen, setIsOpen] = React.useState(false);
  const [modalIsOpen2, setIsOpen2] = React.useState(false);
  
  function openModal() {
    setIsOpen(true);
  }

  function closeModal() {
    setIsOpen(false);
  }
  
  function openModal2() {
    setIsOpen2(true);
  }

  function closeModal2() {
    setIsOpen2(false);
  }

  let location = useLocation();
  if (location.pathname == "/informacoes") {
    let data:any = localStorage.getItem('fardamentos')
    data = JSON.parse(data)
    
    let vtr:any = localStorage.getItem('viaturas')
    vtr = JSON.parse(vtr)
    return (
      <div className="content-informacoes">
        <div className="content">
            <header>
              <h1>Lista de Informações</h1>
            </header>
              <button className="opcoes" onClick={openModal}>Fardamento</button>
              <button className="opcoes" onClick={openModal2}>Viaturas</button>
              <Modal
              isOpen={modalIsOpen}
              onRequestClose={closeModal}
              className="funcionarios-modal"
            >
              <div className="modal-informacoes">
                <div className="modal">
                  <div className="content">
                    <h1>Fardamento</h1>
                    <p>basta copiar a linha e colar no F8</p>
                    <DataTable
                      columns = {columns}
                      data = {data}
                      data-tag = "allowRowEvents"
                      customStyles = {customStyles}
                      theme="solarized"
                      fixedHeader
                    />
                  </div>
                </div>
              </div>
            </Modal>
            <Modal
              isOpen={modalIsOpen2}
              onRequestClose={closeModal2}
              className="funcionarios-modal"
            >
              <div className="modal-informacoes">
                <div className="modal">
                  <div className="content">
                    <h1>Viaturas</h1>
                    <p>Lista de viaturas disponíveis para cada patente</p>
                    <DataTable
                      columns = {columnsViaturas}
                      data = {vtr}
                      data-tag = "allowRowEvents"
                      customStyles = {customStyles}
                      theme="solarized"
                      fixedHeader
                    />
                  </div>
                </div>
              </div>
            </Modal>
        </div>
      </div>
    );
  } else{
    return(<div></div>);
  }
};

export default Informacoes;
