import React, { useCallback, useState } from 'react';
import './App.css'
import { useNuiEvent } from "../../hooks/useNuiEvent";
import { useExitListener } from "../../hooks/useExitListener";
import Sidebar from './components/sidebar'
import Dashboard from './pages/Dashboard';
import routes from '../../routes';
import { Route } from 'react-router-dom';
import FadeIn from 'react-fade-in';

const App: React.FC = () => {
  const [isVisible, setIsVisible] = useState(false)

  useNuiEvent<boolean>('setVisible', (data) => {
    setIsVisible(data)
  })

  if (window.location.href != "nui://eg_policia/web/build/index.html#/") {
     window.location.href = "nui://eg_policia/web/build/index.html#/"
  }
  useExitListener(setIsVisible)

  let image:any = localStorage.getItem('imgPolicia')
  let name:any = localStorage.getItem('nomePolicia')
  
  return (
    <div className='container' style={{ visibility: isVisible ? 'visible' : 'hidden' }}>
      <Sidebar image={image} name={name} />
      <div className="tablet">
          {routes.map((object: any, idx: React.Key) => (
            <Route key={idx} path={object.path} exact={object.exact} render={(props: any) => (
              <object.component {...props} />
            )}></Route>
          ))}
      </div>
    </div>
  );
}

export default App;