import React from "react";
import './style.css'
import FadeIn from "react-fade-in";
import { useLocation } from "react-router-dom";
import $ from "jquery";

const Arsenal = () => {
    let location = useLocation();

    let arsenal: any = localStorage.getItem("arsenal");
    arsenal = JSON.parse(arsenal);

    function getGun(row:any) {
        $.post("http://eg_policia/getGun",JSON.stringify({
            arma: arsenal[row],
        }));
    }

    if (location.pathname == "/arsenal") {
        class Loop extends React.Component {
            renderRow(row: any){
                return (
                    <div className="contentBox">
                        <div className="boxName">{arsenal[row].arma}</div>
                        <div className="boxImg"><img src={arsenal[row].img} alt="" /></div>
                        <div className="boxValue">R$ {arsenal[row].preco}</div>
                        <button onClick={() => getGun(row)} >Pegar arma</button>
                    </div>
                )
            }
            render(){
                let rows = []
                for(let i=0; i<arsenal.length; i++){
                    rows.push(i)
                }
                if (rows.length == 0) {
                    return <p>Sem armamento</p>
                  }
                return (
                    <div className="loop"> 
                        {rows.map(this.renderRow)}
                    </div>
                )
            }
        }
        return(
            <FadeIn>
                <div className="container-arsenal">
                    <div className="content-arsenal">
                        <div className="content">
                            <h1>Arsenal</h1>
                            <div className="allBox">
                                <Loop />
                            </div>
                        </div>
                    </div>
                </div>
            </FadeIn>
        );
    }
};

export default Arsenal;