import React from 'react';
import Acoes from './pages/Home/pages/Acoes';
import Boletim from './pages/Home/pages/Boletim';
import Arsenal from './pages/Home/pages/Arsenal';
import CodigoPenal from './pages/Home/pages/CodigoPenal';
import Dashboard from './pages/Home/pages/Dashboard';
import Funcionarios from './pages/Home/pages/Funcionarios';
import Procurar from './pages/Home/pages/Procurar';
import Relatorios from './pages/Home/pages/Relatorios';
import Informacoes from './pages/Home/pages/Informacoes';

const routes = [
  { path: '/', exact: true, component: Dashboard },
  { path: '/boletim', exact: true, component: Boletim },
  { path: '/arsenal', exact: true, component: Arsenal },
  { path: '/codigo-penal', exact: true, component: CodigoPenal },
  { path: '/funcionarios', exact: true, component: Funcionarios },
  { path: '/acoes', exact: true, component: Acoes },
  { path: '/relatorios', exact: true, component: Relatorios },
  { path: '/informacoes', exact: true, component: Informacoes },
  { path: '/procurar', exact: true, component: Procurar },
];

export default routes;
