import React from "react";
import './style.css'
import FadeIn from "react-fade-in";
import $ from "jquery";

const Boletim = () => {
    const multarBoletim = () => {
        $.post("http://eg_policia/multar",JSON.stringify({
            id: $("#idPessoaMultada").val(),
            amount: $("#valorDaMultaBoletim").val(),
            motivo: $("#motivoDaMultaBoletim").val(),
        }));
    }
    return (
        <div className="content-multas">
            <FadeIn>
            <div className="content">
                <h1>Multar</h1>
                
                <label htmlFor=""> </label>
                <input id="idPessoaMultada" type="number" placeholder="ID da pessoa" required />
                <label htmlFor=""> </label>
                <input id="valorDaMultaBoletim" type="number" placeholder="Valor da multa $" required />
                <label htmlFor=""> </label>
                <input id="motivoDaMultaBoletim" type="text" placeholder="Motivo da multa" required />
                
                <div className="button">
                    <button onClick={multarBoletim}>Multar</button>
                </div>
            </div>
            </FadeIn>
        </div>
    )
}

export default Boletim;