//     hover: "rgba(0,0,0,.08)",
//     disabled: "rgba(0,0,0,.12)",
//   },
// });

let pessoa: string | any[] = []
const handleChange = (state: { selectedRows: any; }) => {
  pessoa = state.selectedRows
};
pessoa[0] = {"label":"","patente":"","pontuacao":0,"user_id":0,"motivo":"","exonerado":0,"advertencia":0,"unidade":"","value":0,"id":0,"nome":""}

const Funcionarios = () => {
  useNuiEvent('updateFuncionario',(data) => {
    localStorage.setItem('funcionarios',JSON.stringify(data))
    return(<></>);
  })
  
  useEffect(() => {
    let funcionarios: any = localStorage.getItem("funcionarios");
    funcionarios = JSON.parse(funcionarios);
  });

  const columns = [
    {
      name: "N°:",
      selector: (data: any) => data.id,
      sortable: true,
      maxWidth: "1vw",
      minWidth: "4.75vw"
    },
    {
      name: "Nome:",
      selector: (data: any) => data.nome,
      sortable: true,
      style: {
      },
    },
    {
      name: "ID:",
      selector: (data: any) => data.user_id,
      sortable: true,
      maxWidth: "2vw",
    },
    {
      name: "Patente:",
      selector: (data: any) => data.patente,
      sortable: true,
    },
    {
      name: "Pontos:",
      selector: (data: any) => data.pontuacao,
      sortable: true,
      maxWidth: "1vw",
    },
    {
      name: "ADVS:",
      selector: (data: any) => data.advertencia,
      sortable: true,
    },
    {
      name: "Unidade:",
      selector: (data: any) => data.unidade,
      sortable: true,
    },
  ];

  const [modalIsOpen, setIsOpen] = React.useState(false);
  const [modalIsOpen2, setIsOpen2] = React.useState(false);
  const [modalIsOpen3, setIsOpen3] = React.useState(false);
  
  function openModal() {
    setIsOpen(true);
  }

  function closeModal() {
    setIsOpen(false);
  }

  function openModal2() {
    setIsOpen2(true);
  }

  function closeModal2() {
    setIsOpen2(false);
  }

  function openModal3() {
    setIsOpen3(true);
  }

  function closeModal3() {
    setIsOpen3(false);
  }

  let selectPatenteValue = ""
  function selectPatente(newValue:any, actionMeta:any) {
    selectPatenteValue = newValue
  }  
  
  let selectUnidadeValue = ""
  function selectUnidade(inputValue:any, actionMeta:any) {
    selectUnidadeValue = inputValue
  }

  function editUser() {
    $.post("http://eg_policia/editarFuncionario",JSON.stringify({
      id: $("#editID").val(),
      name: $("#editName").val(),
      pontuacao: $("#editPontuacao").val(),
      adv: $("#editAdv").val(),
      unidade: selectUnidadeValue,
      patente: selectPatenteValue,
    }));
    closeModal();
  }

  const cadastrarFuncionario = () => {
    $.post(
      "http://eg_policia/cadastrarFuncionario",
      JSON.stringify({
        id: $("#contratarFuncID").val(),
      })
    );
    closeModal2();
  };

  const exonerarFuncionario = () => {
    $.post(
      "http://eg_policia/exonerarFuncionario",
      JSON.stringify({
        id: $("#exonerarFuncID").val(),
        desc: $("#exonerarFuncDesc").val(),
      })
    );
    closeModal3();
  };

  let location = useLocation();
  if (location.pathname == "/funcionarios") {
    let funcionarios: any = localStorage.getItem("funcionarios");
    funcionarios = JSON.parse(funcionarios);

    let patentes: any = localStorage.getItem("patentes");
    patentes = JSON.parse(patentes);

    let unidades: any = localStorage.getItem("unidades");
    unidades = JSON.parse(unidades);

    let isComando: any = localStorage.getItem("isComando");
    if (isComando == 1) {
      return (
        <div className="content-funcionarios">
          <div className="content">
            <FadeIn>
              <header>
                <h1>Lista de funcionários</h1>
                <button className="editar" onClick={openModal}>
                  Editar
                </button>
                <button className="contratar" onClick={openModal2}>
                  Contratar
                </button>
                <button className="exonerar" onClick={openModal3}>
                  Exonerar
                </button>
              </header>
              <div className="data-table-funcionarios">
                <DataTable
                  columns={columns}
                  data={funcionarios}
                  selectableRows
                  data-tag="allowRowEvents"
                  customStyles={customStyles}
                  theme="solarized"
                  fixedHeader
                  onSelectedRowsChange={handleChange}
                />
              </div>
              <Modal
                isOpen={modalIsOpen}
                onRequestClose={closeModal}
                className="funcionarios-modal"
              >
                <div className="modal-funcionarios">
                  <div className="modal">
                    <div className="content">
                      <h1>Editar funcionário</h1>
                      <p>Aqui você pode editar os dados de um funcionário</p>
                      <div className="inputs">
                        <label htmlFor="">Nome:</label>
                        <input id="editName" type="text" placeholder={pessoa[0].nome} />
                        <input id="editID" type="text" placeholder={pessoa[0].user_id} value={pessoa[0].user_id} hidden disabled />
                        
                        <label htmlFor="">Alterar Patente: </label>                    
                        <Select
                          className="select"
                          closeMenuOnSelect={false}
                          defaultValue={pessoa[0].patente}
                          components={animatedComponents}
                          options={patentes}
                          onChange={selectPatente}
                        />
  
                        <label htmlFor="">Alterar Unidade: </label>
                        <Select
                          className="select"
                          closeMenuOnSelect={false}
                          defaultValue={pessoa[0].unidade}
                          components={animatedComponents}
                          options={unidades}
                          onChange={selectUnidade}
                        />
  
                        <label htmlFor="">Alterar Advertência:</label>
                        <input id="editAdv" type="number" placeholder={pessoa[0].advertencia} />
  
                        <label htmlFor="">Adicionar Pontução:</label>
                        <input id="editPontuacao" type="number" placeholder={pessoa[0].pontuacao} />
  
                      </div>
                      <button onClick={editUser}>CONFIRMAR</button>
                    </div>
                  </div>
                </div>
              </Modal>
              <Modal
                isOpen={modalIsOpen2}
                onRequestClose={closeModal2}
                className="funcionarios-modal"
              >
                <div className="modal-funcionarios">
                  <div className="modal">
                    <div className="content">
                      <h1>Contratar funcionário</h1>
                      <p>Aqui você pode contratar um funcionário</p>
                      <div className="inputs">
                        <label htmlFor="">ID:</label>
                        <input
                          id="contratarFuncID"
                          type="number"
                          placeholder="1"
                        />
                      </div>
                      <button onClick={cadastrarFuncionario}>CONTRATAR</button>
                    </div>
                  </div>
                </div>
              </Modal>
              <Modal
                isOpen={modalIsOpen3}
                onRequestClose={closeModal3}
                className="funcionarios-modal"
              >
                <div className="modal-funcionarios">
                  <div className="modal">
                    <div className="content">
                      <h1>Exonerar funcionário</h1>
                      <p>Aqui você pode contratar um funcionário</p>
                      <div className="inputs">
                        <label htmlFor="">ID:</label>
                        <input
                          id="exonerarFuncID"
                          type="number"
                          placeholder="1"
                        />
                        <label htmlFor="">Motivo:</label>
                        <input
                          id="exonerarFuncDesc"
                          type="text"
                          placeholder="Má conduta"
                        />
                      </div>
                      <button className="exonerar-btn" onClick={exonerarFuncionario} > EXONERAR </button>
                    </div>
                  </div>
                </div>
              </Modal>
            </FadeIn>
          </div>
        </div>
      );
    } else {
      return (
        <div className="content-funcionarios">
          <div className="content">
            <FadeIn>
              <header>
                <h1>Lista de funcionários</h1>
              </header>
              <div className="data-table-funcionarios">
                <DataTable
                  columns={columns}
                  data={funcionarios}
                  data-tag="allowRowEvents"
                  customStyles={customStyles}
                  theme="solarized"
                  fixedHeader
                  onSelectedRowsChange={handleChange}
                />
              </div>
            </FadeIn>
          </div>
        </div>
      );
    }
  }
};

export default Funcionarios;
